/* Modern button styles */
button {
    background-color: #4caf50; /* Green color */
    color: #fff; /* White text color */
    padding: 12px 24px;
    border: none;
    border-radius: 5px; /* Rounded corners */
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #45a049; /* Slightly darker green on hover */
  }
  
  /* Style the input areas */
  input {
    padding: 10px;
    border: 1px solid #ccc; /* Gray border */
    border-radius: 5px; /* Rounded corners */
    width: 100%;
    font-size: 16px;
  }
  
  /* Style the text editor textarea */
  textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc; /* Gray border */
    border-radius: 5px; /* Rounded corners */
    resize: none; /* Prevent textarea from being resized by the user */
    font-family: 'Arial', sans-serif; /* Change the font to a sans-serif for better readability */
    font-size: 14px;
    line-height: 1.4;
    background-color: #f5f5f5; /* Light gray background */
  }
  
  /* Modern button style for the "Join Room" button */
  #join-room {
    background-color: #007bff; /* Blue color */
    color: #fff; /* White text color */
    padding: 12px 24px;
    border: none;
    border-radius: 5px; /* Rounded corners */
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  #join-room:hover {
    background-color: #0056b3; /* Slightly darker blue on hover */
  }